import React from 'react'
import {Spinner} from "@vkontakte/vkui";
import StockCard from "./StockCard";

const CurrenciesGroup = (p) => {
    if(p.cash.length === 0 || p.rates.length === 0){
        return <Spinner size="large"/>
    }
    let v;
    let s = {
        usd: '$',
        eur: '€',
        rub: '₽',
        gbp: '£',
        chf: '₣'
    }
    return Object.keys(p.cash).map(k => {
        v = p.cash[k];
        if((v.count !== 0 || !p.p) && ((k === 'rub' && p.p) || !(!p.p && k === 'rub'))){
            return <StockCard
                key={v.count+v.avgPrice+k}
                a={p.p ? v.avgPrice : p.rates[k].valPrev}
                b={p.p ? v.count : 1}
                c={p.rates[k].val}
                v={p.rates[k].ticker}
                p={p.p}
                s={p.s}
                z={'₽'}
                o={s[k]}
            />
        }
        return '';
    });
}

export default CurrenciesGroup;