import React from "react";
import {Avatar, Caption, Cell, Spinner, Title} from "@vkontakte/vkui";
import StocksData from "../Functions/StocksData";
import bridge from "@vkontakte/vk-bridge";

const StockCard = (p) => {
    let c = p.c;
    if(c && p.p){
        c = c.toFixed(2);
    }
    let v = p.v;
    let a = p.a;
    let b = p.b;
    let z = p.z;
    let d = StocksData[v];
    let x = ((c / a) * 100 - 100).toFixed(2);
    let y = c - a;
    if(b < 0){
        x *= -1;
        // y *= -1;
    }
    return <Cell
        onClick={() => {
            bridge.send("VKWebAppTapticImpactOccurred", {"style": "light"});
            p.s('stock', {
                ticker: v,
                name: d.name,
                price: parseFloat(c),
                count: p.p ? b : 0,
                avgPrice: a
        })}}
        before={
            <Avatar
                size={32}
                src={require(`../Logos/${d.logo}`).default}
            />}
        description={p.p && p.c ?
            <Caption
                style={v === 'RUBRUB' ? {display: 'none'} : {}}
                level={2}
                weight={"regular"}>
                {`${new Intl.NumberFormat('ru-RU').format(b.toFixed(2))} ${p.o} · ${new Intl.NumberFormat('ru-RU').format(c)} ${z}`}
            </Caption>
            : ''}
        indicator={
            p.c ? <div style={{textAlign: "right"}}>
            <Title
                style={{color: 'var(--header_text)'}}
                level={3}
                weight={"medium"}>
                {p.p ? new Intl.NumberFormat('ru-RU').format((b*c).toFixed(2)) : new Intl.NumberFormat('ru-RU').format((c).toFixed(2))} {z}
            </Title>
            <Caption
                style={
                    v === 'RUBRUB' ? {display: 'none'} :
                        x !== 0 ? (
                            x > 0 ?
                                {color: 'var(--dynamic_green)'} :
                                {color: 'var(--dynamic_red)'}
                        ) : {color: 'var(--dynamic_gray)'}
                }
                level={2}
                weight={"regular"}>
                {p.p ? new Intl.NumberFormat('ru-RU').format((b*y).toFixed(2)) : new Intl.NumberFormat('ru-RU').format((y).toFixed(2))} {z} ({x}%)
            </Caption>
        </div> : <Spinner/>}
    >
        <Title level={3} weight={"semibold"}>{d.name}</Title>
    </Cell>
}

export default StockCard;